/*Info*/
/*Start*/


.info {
    & .template-title::before {
        background: $color-secondary;
    }
    &-container {
        display: flex;
        justify-content: center;
        &__text {
            font-size: 17px;
            line-height: 156.5%;
            max-width: 555px;
            width: 100%;
            margin-right: 30px;
            margin-bottom: 40px;
            & p {
                margin-bottom: 30px;
            }
        }
        img {
            box-shadow: 5px 15px 40px rgba(11, 24, 97, 0.32);
        }
        &:nth-child(odd)  {
            justify-content: space-between;
        }
    }
    & .direction-row-reverse .info-container__text {
        margin-top: 23px;
        margin-right: 0;
        margin-left: 45px;
        max-width: 523px;
    }
    & .btn-information {
        margin-top: 18px;
        max-width: 243px;
        width: 100%;
        text-align: left;
        background-position-x: 195px;
        box-shadow: none;
        &:hover {
            background-position-x: 195px;
        }
    }
     
}



