/*Main slider*/
/*Start*/

.first-screen {
    position: relative;
    &-slider {
        position: relative;
        &__title {
            font-weight: 700;
            font-size: 56px;
            line-height: 126%;
            text-transform: uppercase;
            max-width: 500px;
            margin-bottom: 8px;
        }
        &__subtitle {
            font-weight: 400;
            font-size: 20px;
            line-height: 135.5%;
            max-width: 500px;
            padding-right: 160px;
            margin-bottom: 37px;
        }
        &__btn {
            background-position-x: 245.52px;
            border-radius: 90px;
            display: inline-flex;
            text-decoration: none;
            min-width: 295px;
            &:hover {
                background-position-x: 245.52px;

            }
        }
        & .slick-slide{
            padding: 163px 0 90px;
            background-repeat: no-repeat;
            background-size: cover;
            border-radius: 20px;
            min-height: 700px;
            
        }
        & .slick-dots {
            width: 110px;
        }
    
    }
    &__container-arrow {
        position: absolute;
        z-index: 3;
        max-width: 1523px;
        width: 100%;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        display: flex;
        justify-content: space-between;
    } 
}





.slick-dots {
    padding: 0;
    display: flex;
    justify-content: space-between;
    position: absolute;
    bottom: 28px;
    right: 50%;
    transform: translate(50%, 0);
    & li {
        list-style: none;
        padding: 0;
        font-size: 0;
        & button {
            width: 8px;
            height:8px;
            border-radius: 50%;
            padding: 0;
            border: 3px solid rgb(255, 255, 255);
            background: transparent;
        }
    }
    & .slick-active  button {
        background-color: rgb(255, 255, 255);
    }
    
}



.slick-arrow {
    font-size: 0;
    border: none;
    padding: 0;
    width: 46px;
    height: 46px;   
    background: url("data:image/svg+xml,%3Csvg width='24' height='42' viewBox='0 0 24 42' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M20.5976 0.745693C20.8829 0.460422 21.2395 0.317787 21.6317 0.317787C22.024 0.317787 22.3806 0.460422 22.6658 0.745693C23.2364 1.31624 23.2364 2.24337 22.6658 2.81391L4.4798 21L22.6658 39.186C23.2364 39.7565 23.2364 40.6837 22.6658 41.2542C22.0953 41.8248 21.1682 41.8248 20.5976 41.2542L1.37747 22.0341C0.806928 21.4635 0.806929 20.5364 1.37747 19.9658L20.5976 0.745693Z' fill='white'/%3E%3C/svg%3E%0A") no-repeat;
    background-position: center;
    &:hover {
        background: url("data:image/svg+xml,%3Csvg width='24' height='42' viewBox='0 0 24 42' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M20.5976 0.746181C20.8829 0.460911 21.2395 0.318275 21.6317 0.318275C22.024 0.318275 22.3806 0.460911 22.6658 0.746181C23.2364 1.31673 23.2364 2.24386 22.6658 2.8144L4.4798 21.0004L22.6658 39.1865C23.2364 39.757 23.2364 40.6842 22.6658 41.2547C22.0953 41.8253 21.1682 41.8253 20.5976 41.2547L1.37747 22.0346C0.806928 21.464 0.806929 20.5369 1.37747 19.9663L20.5976 0.746181Z' fill='%2389C249'/%3E%3C/svg%3E%0A") no-repeat;
    }
}

.slick-next {    
    transform: rotate(180deg);
}

/*End*/
