/*Delivery*/
/*Start*/

.delivery {
    position: relative;
    & .container {
        max-width: 1210px;
    }
    & .template-title {
        margin-top: $section-margin;
    }
    & .slider-delivery__item {
        margin: 0 15px;
        background: $bg-brand;
        border-radius: 20px;
        overflow: hidden;
        padding: 0 20px 37px;
        width: 100%;
        max-width: 360px;
        margin-bottom: $section-margin;
        box-shadow: 5px 15px 40px rgba(11, 24, 97, 0.32), 0px 4px 13px rgba(30, 35, 66, 0.11);
        & .delivery-img {
            margin: 0 -20px 27px;
            & img {
                width: 100%;
                height: 229px;
                object-fit: cover;
                object-position: center top;
                margin-bottom: 11px;
            
            }
        }
        &-title {
            @include slider-title;
            text-align: center;
        
        }
        &-info {
            display: flex;
            justify-content: center;
            margin-bottom: 14px;
        }
        
        &-info-day,
        &-info-weight {
            margin: 0 15px;
            background-repeat: no-repeat;
            background-position: 0 center;
            padding-left: 34px;
        }
        
        &-info-day{
            background-image: url('assets/img/icon-calendar.svg');
        }
        
        &-info-weight {
            background-image: url('assets/img/icon-box.svg');
        }
        
        &-price {
            font-size: 22px;
            line-height: 1.1;
            text-align: center;
            color: #A3A3A3;
            margin-bottom: 18px;
            & b {
                font-weight: 700;
                font-size: 45px;
                line-height: 56.03px;
                color: white;
            }
            & span {
                color: white;
            }
        }
        
        &-row-btn {
            display: flex;
            justify-content: space-between;
            & .btn {
                padding: 14px 11px;
                width: 153px;
                background-image: none;
                font-size: 15px;
             
            }
        }
    }
    & .container-arrow{
        max-width: 1323px;
        width: 100%;
        display: flex;
        justify-content: space-between;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, 0);
    }
    & .slick-dots {
        max-width: 110px;
        width: 100%;
    }
}


/*End*/

