/*Form*/
/*Start*/

.form-calc {
    position: relative;
    & .container {
        display: flex;
        flex-direction: column;
        position: relative;
        &::after{
            content: '';
            position: absolute;
            background: url('assets/img/man.png') no-repeat;
            max-width: 625px;
            width: 100%;
            height: 100%;
            background-position: bottom right;
            margin-left: 582px;  
        }
    }
    & .template-title {
        max-width: 600px;
        padding-bottom: 0;
        & h2 {
            margin-top: $section-margin;
            text-align: left;
        }
        &::before{
            content: '';
            position: absolute;
            bottom: 16%;
            right: 0;
            transform: translate(0, 0);
            background: $color-secondary;
            width: 85px;
            height: 2px;
            margin-right: 25px;
        }
    }
    & form {
        position: relative;
        width: 100%;
        max-width: 626px;
        background: #363A4D;
        border-radius: 20px;
        margin-top: 4px;
        margin-bottom: 113px;
        & .container-form {
            display: flex;
            justify-content: space-between;
            margin: 53px 50px 0;
            &::after {
                content: "";
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                background: rgb(0 0 0 / 40%) url(assets/img/gif-download.gif) center / 50px no-repeat;
                border-radius: 20px;
                opacity: 0;
                visibility: hidden;
            }
            &__item {
                width: 100%;
                margin: 0 7px;
                &-file {
                    margin: 14px 57px 26px;
                    & .upload-btn {
                        width: 100%;
                        max-width: 526px;
                        background: transparent;
                        border: 1px dashed rgba(255, 255, 255, 0.26);
                        border-radius: 10px;
                        padding: 12px 93px 17px;
                        font-size: 15px;
                        color: white;
                        line-height: 126%;
                        text-align: center;
                        cursor: pointer;
                    }
                    
                    & #upload-text {
                        display: block;
                        margin-bottom: 12px;
                    }            
                }
            }
            & .form-item {
                display: flex;
                flex-direction: column;
                &__title {
                    font-weight: 600;
                    font-size: 15px;
                    line-height: 126%;
                    margin-bottom: 6px;
                }
                
                &__field {
                    padding: 19px 20px;
                    background: #FFFFFF;
                    border: none;
                    border-radius: 10px;
                    font-size: 15px;
                    line-height: 126%;
                    color: #CBCBCB;
                    margin-bottom: 20px;
                    color: #282B38;
                    &._error {
                        box-shadow: 0 0 15px red;
                    }
                    &:hover {
                        box-shadow: 0 0 15px white;
                    }
                    &:focus {
                        box-shadow: 0 0 15px $color-secondary;
                        border-color: transparent;
                    }
                }
                &__check {
                    display: flex;
                    & input {
                        margin-bottom: 14px;
                    }
                    
                    & label {
                        margin: 3px 0 16px 9px;
                    }
                    & .checkbox-design {
                        appearance: none;
                        background: #FFFFFF;
                        border: 1px solid #282B38;
                        border-radius: 4px;
                        width: 24px;
                        height: 24px;
                        cursor: pointer;
                        &:checked {
                            background: url('assets/img/icon-check.svg') 0 0 no-repeat;
                            background-color: white;
                            background-position: center;
                        }
                    }
                    
                }     
            } 
        }
        & .btn-form {
            margin: 0 50px 58px;
            max-width: 253px;
            width: 100%;
            text-align: left;
            background-position-x: 205.45px;                    
            &:hover, &:focus {
                background-position-x: 205.45px;
            }
        }
        & ._sending .container-form {
            opacity: 1;
            visibility: visible;
        }    
    }
}




/*End*/