$color-primary: #E58D35;
$color-secondary: #89C249;
$bg-brand: #363A4D;
$section-margin: 100px;
$section-margin-mobile: 55px;
$slider-gradient: linear-gradient(0deg, rgba(4, 5, 17, 0.91) 0%, rgba(88, 88, 88, 0) 100%);

@mixin f-screen-before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

@mixin slider-title {
    font-weight: 700;
    font-size: 22px;
    line-height: 124.5%;
}

