/*FaQ*/
/*Start*/

.faq {
    & .container {
        margin-bottom: $section-margin;
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    }
    & .template-title {
        margin-top: $section-margin-mobile;
    }
    & .accordion-faq {
        width: 100%;
        max-width: 716px;
    }
    .form-faq {
        & form {
            display: flex;
            flex-direction: column;
            background: #363A4D;
            box-shadow: 5px 15px 40px rgba(11, 24, 97, 0.32);
            border-radius: 20px;
            width: 100%;
            max-width: 395px;
        }
        &__content {
            display: flex;
            flex-direction: column;
        }
        &__title {
            font-weight: 700;
            font-size: 24px;
            line-height: 126%;
            margin-bottom: 0;
            padding: 36px 45px 8px 40px;
        }
        &__subtitle {
            font-size: 17px;
            line-height: 126%;
            padding: 0 45px 23px 40px;
            margin-bottom: 0;
        }
        &__content-field {
            margin: 0 40px 14px;
            padding: 19px 20px 18px;
            border-radius: 10px;
            border: none;
            &:hover{
                box-shadow: 0 0 15px white;
            }
            &:focus {
                box-shadow: 0 0 15px $color-secondary;
                border-color: transparent;
            }
            
        }
        &__content-textarea {
            margin: 0 40px 26px;
            padding: 19px 20px 0px;
            border-radius: 10px;
            border: none;
            &:hover{
                box-shadow: 0 0 15px white;
            }
            &:focus {
                box-shadow: 0 0 15px $color-secondary;
                border-color: transparent;
            }
            
        }
        
        &__button {
            width: 100%;
            max-width: 310px;
            text-align: left;
            margin: 0 40px 37px;
            background-position-x: 265px;     
            &:hover {
                background-position-x: 250px;
            }
        }

    }
    & .accordion-faq {
        & .accordion-item {
            margin-bottom: 16px;
            border-radius: 10px;
            box-shadow: none;
            border: none;
            width: 100%;
            max-width: 716px;
            
        }
        & .accordion-button {
            transition: .4s;
            &:not(.collapsed) {
                box-shadow: none;
                background: none;
                font-weight: 700;
                font-size: 18px;
                line-height: 124.5%;
                color: #282B38;
                padding: 24px 34px;
            }
            &:focus { 
                box-shadow: none;
            }
            &::after {
                background-image: url('assets/img/arrow-circle-left-white.svg');  
            }
            &:not(.collapsed)::after { 
                transform: rotate(-90deg);
                background-image: url('assets/img/arrow-circle-left-orange.svg');
            }
            &.collapsed {
                border: 1px solid #3A444E;
                border-radius: 8px;
                background-color: #282B38;
                color: white;
                padding: 21px 34px;
            }
            
            &.collapse{
                border-radius: 8px;
            }                 
        }
        & .accordion-body {
            padding: 0 99px 21px 33px;
            font-size: 17px;
            line-height: 144.5%;
            color: #282B38;
            
        }
        
        &.accordion-item:last-of-type .accordion-button.collapsed {
            border-radius: 8px;
        }
    }   
}

/*End*/


