/*Advantages*/ 
/*Start*/
.advantages-slider {
    position: relative;
    margin-top: -48px;
    &__item-content {
        margin: 48px auto 0;
        max-width: 263px;
        text-align: center;
        & .img-box {
            width: 113px;
            height: 113px;
            margin: 0 auto 22px;
            display: flex;
            justify-content: center;
            align-items: center;
            border: 1px solid #6C7188;
            border-radius: 90px;
            & img {
                max-width: 100%;
                background: $color-secondary;
                width: 93px;
                height: 93px;
                object-fit: none;
                border-radius: 50%;
            }    
        }
        & .text-box {
            font-size: 18px;
            line-height: 124.5%;
        }
    }
    & .slick-dots {
        display: flex;
        justify-content: space-between;
        width: 268px; 
        bottom: -50px;
    }
}


/*End*/