@media (max-width: 1709.98px) {
    .first-screen-slider .slick-slide .container {
        padding-left: 100px;
    }

    .first-screen__container-arrow {
        max-width: 1290px;
        padding: 0 50px;
    }
}

@media (max-width: 1300px) {
    .form-calc .container::before {
        max-width: 578px;
    }
    .delivery .container, .services .container {
        max-width: 780px;
    }
    .slider-item__row-btn .btn {
        width: 100%;
        max-width: 140px;
        padding: 14px 10px;
    }
    .container-arrow {
        padding: 0 50px;
    }
}

@media (max-width: 1231.98px) {
    .about {
        & .container {
            justify-content: center
        }
        & .template-title {
            margin-bottom: 25px;
            & h2{
                text-align: center;
            }
            &::before {
                right: 50%;
                transform: translate(50%, 0);
            }
        }

    }
    .container-about {
        &__image {
            margin-top: 25px;
        }
        &__content-info {
            text-align: justify;
        }
    }
    .faq {
        & .container {
            justify-content: center;
        }
        & form {
            max-width: 800px;
        }
        & .template-title {
            margin-left: 12px;
            margin-right: 12px;
            margin-bottom: 25px;
        }
    }
    .accordion-faq .accordion-item:last-of-type .accordion-button.collapsed {
        margin-bottom: 28px;
        justify-content: space-between;
    }
    .faq {
        & .form-faq {
            &__title, &__subtitle {
                text-align: center;
            }
            &__content-field {
                margin: 0 20px 14px;
            }
            &__content-textarea {
                margin: 0 20px 26px;
            }
            &__button {
                margin: 0 20px 38px;
                max-width: 295px;
                background-position-x: 250px;
            }
        }
    }
    .info {
        &-container {
            flex-direction: column-reverse;
            align-items: center;
            &__text {
                margin-right: 0;
                text-align: justify;
                margin-top: 36px;
                margin-bottom: 13px;
            }
        }
        & .direction-row-reverse .info-container__text{
            margin-left: 0;
            margin-bottom: 0;
        } 
        & .btn-information {
            max-width: 335px;
            background-position-x: 288px;
            &:hover {
                background-position-x: 288px;
            }
        }
    }  
    }



@media (max-width: 1204.98px) {
    .navbar {
        & .container {
            padding: 0 50px;
        }
        &-expand-lg .navbar-nav .nav-link {
            padding-right: 15px;
        }
    }
    .form-calc {
        & .container {
            align-items: center;
            padding: 0 12px;
            &::after {
                background: none;
                margin: 0 auto;
                z-index: -1;
            }
        }
        & form {
            max-width: 800px;
        }
    }
}

@media (max-width: 991.98px) {
    .navbar {
        &-collapse .navbar-nav {
            display: flex;
            align-items: center;
            justify-content: center;
            background: #363A4D;
            & .nav-link {
                padding: 37px 0 0 0;
                background-color: #363A4D;
            }
        }
        &-brand {
            margin: 6px 20px 18px;
        }
        &-button {
            background-color: #363A4D;
            display: flex;
            justify-content: center;
            margin: -1px;
            padding: 35px 0 60px;
            margin-bottom: 97px;    
        }
        .btn__header {
            width: 295px;
        }
    }

    .first-screen-slider .slick-slide {
        background: linear-gradient(0deg, rgba(4, 5, 17, 0.6), rgba(4, 5, 17, 0.6)), url(transportation-logistics.jpg);
        background-position: bottom;
    }

    .form-calc {
        & .template-title {
            padding-bottom: 21px;
            &::before {
                bottom: 0;
                right: 50%;
                transform: translate(50%, 0);
                margin-right: 0;
            }
            & h2 {
                font-size: 24px;
                text-align: center;
            }
        }
        & #upload-btn {
            max-width: 800px;
        }
    }
    .template-title h3 {
        font-size: 16px;
        line-height: 20px;
    }
    .template-title h2 {
        font-size: 24px;
    }
    .delivery {
        & .container {
            max-width: 400px;
        }
        & .slider-delivery__item {
            margin: 0;
            margin-bottom: $section-margin;
        }
    }
    .services {
        & .container {
            max-width: 400px;
        }
        & .slider-services__item {
            margin: 0;
            margin-bottom: $section-margin;
        }
    }
    .accordion-faq {
        & button.accordion-button.collapsed, .accordion-faq .accordion-button:not(.collapsed) {
            padding: 12px 22px;
        }
        & .accordion-body {
            padding: 0 15px 12px 22px;
        }
    }

    footer {
        & .container {
            justify-content: center;
        } 
        & .nav-box {
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            &:nth-child(n+2){
                display: none;
            }
            &__content {
                display: flex;
                flex-direction: column;
                align-items: center;
            }
        }
    }
}

@media (max-width: 768.98px) {
    .navbar .container {
        padding: 0;
    }
    section {
        padding: 0;
    }
    .bg-gray{
        margin: 0;
        border-radius: 0;
        padding: 55px 0 60px;
    }
    .first-screen-slider {
        & .slick-slide {
            border-radius: 0;
        }
        & .slick-slide .container {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            padding: 0;
            text-align: center;
            min-height: 300px;
        }
        &__title {
            font-size: 32px;
            margin-bottom: 10px;
            padding: 0 20px;
        }
        &__subtitle {
            font-size: 16px;
            padding: 0 20px;
            margin-bottom: 18px;
        }     
    }
    .form-calc {
        & .template-title h2 {
            margin-top: $section-margin-mobile;
        }
        & form {
            margin-bottom: 60px;
            & .container-form {
                flex-wrap: wrap;
                margin: 53px 30px 0 ;
                &__item:first-child {
                    margin-right: 0;
                }
                &__item:nth-child(2) {
                    margin-left: 0;
                }
            }
            & .btn-form {
                margin: 26px 30px 34px;
                max-width: 275px;
                background-position-x: 230.5px;
                &:hover {
                    background-position-x: 230.5px
                }
            }
            & .container-form__item-file {
                margin: 14px 30px 26px;
                & .upload-btn {
                    padding: 12px 0 17px;
                }
            }
            & .form-item__title {
                font-size: 15px;
            }
            & .form-item__field {
                font-size: 15px;
            }
        }   
    }
    .services {

        & .slider-services__item {
            margin-bottom: 40px;
        }
        & .slider-item__title {
            font-size: 20px;
        }
        & .slick-dots {
            bottom: -16px;
        }
    }

    footer .container::before {
        width: 345px;
    }
    .about .template-title h2 {
        margin-top: $section-margin-mobile;
    }
}