/*About*/
/*Start*/

.about {
    & .container {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    }
    & .template-title{
        & h2 {
            margin-top: 145px;
            text-align: left;
        }
        &::before {
            background: $color-secondary;
            right: auto;
            transform: translate(0, 0);
        }
    }
    & .container-about__content {
        max-width: 555px;
        font-size: 18px;
        line-height: 170%;
    }
    
    & .container-about__image {
        margin-top: 91px;
    }
}



/*End*/

