section {
    padding: 0 50px;
}

body {
    background: #282B38;
    font-family: 'Proxima Nova';
    color: #FFFFFF;
}
.container {
    max-width: 1180px;
    width: 100%;
    padding: 0 20px;
}

.btn {
    background: $color-primary;
    border-radius: 90px; 
    padding: 23px 32px 22px;
    color: white;
    transition: .4s;
    font-weight: 600;
    font-size: 16px;
    line-height: 146%;
    background: $color-primary no-repeat;
    background-image: url('assets/img/arrow-circle-left.svg');
    background-position-y: 50%;
}

.btn:hover, .btn:focus {
    background: $color-secondary;
    color: white;
    background-image: url('assets/img/arrow-circle-left-white.svg');
    background-position-y: 50%;
    background-repeat: no-repeat;
    box-shadow: none;
}

.btn-trans {
    background: transparent;
    border: 1px solid $color-primary;
}

.template-title {
    position: relative;
    padding-bottom: 21px;
    margin-bottom: 55px;
}

.btn-trans:hover, .btn-trans:focus {
    background: $color-primary;
}


.template-title::before {
    content: '';
    position: absolute;
    bottom: 0;
    right: 50%;
    transform: translate(50%, 0);
    background: $color-primary;
    width: 85px;
    height: 2px;
}

.template-title h2{
    font-weight: 700;
    font-size: 36px;
    line-height: 124.5%;
    text-transform: uppercase;
    text-align: center;
}

.template-title h3 {
    line-height: 30px;
    font-size: 24px;
    text-align: center;
    padding-top: 6px;
}

.bg-gray {
    background: rgba(54, 58, 77, 1);
    background: linear-gradient(0deg, #363A4D, #363A4D), #F7F7F7;
    border-radius: 20px;
    padding: 90px 0 100px;
    margin: 0 50px;
}

.direction-row-reverse {
    display: flex;
    flex-direction: row-reverse;
}