.navbar-expand-lg .navbar-nav .nav-link {
    color: white;
    font-size: 16px;
    line-height: 146%;
    padding-right: 37px;
    &:last-child {
        padding-right: 0;
    }    
    &:hover, &:focus {
        color: $color-secondary;
    }
}



.navbar .container {
    margin: 27px auto;
    max-width: 1164px;

}

.navbar-light .navbar-toggler-icon {
    background-image: url('assets/img/burger-menu.svg.svg'); 
    margin: 6px 20px 18px
}

.navbar-toggler {
    padding: 0;
    box-shadow: none;
    border: none;    
    &:focus {
        box-shadow: none;
    }
}


[aria-expanded = true].navbar-toggler .navbar-toggler-icon {
    background-image: url('assets/img/burger-menu-cross.svg');
}

.btn__header {
    background-image: none;
    padding: 14px 27px;
    & a {
        text-decoration: none;
        color: #FFFFFF;
    }
    &:hover, &:focus {
        background: $color-secondary;
        color: #FFFFFF;
    }
}
