footer {
    & .container {
        display: flex;
        justify-content: space-between;
        position: relative;
        margin-top: 92px;
        margin-bottom: 59px;
        &::before {
            content: "";
            position: absolute;
            top: 0;
            height: 2px;
            width: 100%;
            background-color: $color-primary;
            margin-top: -36px;
        }
    }
    & .nav-box {
        &__logo {
            margin-bottom: 33px;
        }
        &__content {
            font-size: 14px;
            line-height: 162.5%;
            text-align: left;
            max-width: 263px;
        }
        &__social {
            margin-top: 20px;
        }
        &__title {
            font-weight: 700;
            font-size: 20px;
            line-height: 146%;
            margin-bottom: 14px;
        }
        &__content-link {
            display: flex;
            flex-direction: column;
            & a{
                font-size: 16px;
                line-height: 160.5%;
                color: #85899C;
            
            }
        }
    }
    & p {
        margin: 0;
    }
    & a:hover {
        color: white;
    
    }
}






 