
/*Services*/
/*Start*/

.services {
    position: relative;
    & .slick-initialized .slick-slide {
        display: flex;
    }
    &-bg-1 {
        background-image: $slider-gradient, url('assets/img/image-services-1.png');
        background-repeat: no-repeat;
        
    }
    &-bg-2 {
        background-image: $slider-gradient, url('assets/img/image-services-2.png');
        background-repeat: no-repeat;
        
    }
    &-bg-3 {
        background-image: $slider-gradient, url('assets/img/image-services-3.png');
        background-repeat: no-repeat;
        
    }
    &-bg-4 {
        background-image: $slider-gradient, url('assets/img/image-services-4.png');
        background-repeat: no-repeat;
        
    }
    & .container {
        max-width: 1210px;
    }

    & .slider-services__item {
        margin: 0 15px;
        border-radius: 20px;
        flex-direction: column;
        justify-content: flex-end;
        height: 482px;
        &-title {
            @include slider-title;
            text-align: left;
            margin: 0 30px 13px;
        }
        &-subtitle {
            font-weight: 400;
            font-size: 15px;
            line-height: 124.5%;
            margin: 0 23px 28px 30px;
        }
    }
    
    & .slick-dots {
        max-width: 110px;
        width: 100%;
        bottom: -59px;
    }

    & .container-arrow-services {
        max-width: 1323px;
        width: 100%;
        display: flex;
        justify-content: space-between;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, 100%);
    }
    & .btn-trans {
        margin: 0 30px 33px;
        padding: 14px 24px;
        width: 100%;
        max-width: 153px;
        background-image: none;
    }
    
}

/*End*/